@import "vars";
@import "mixins";
@import "fonts";
@import "settings";
@import "choices.min";
@import "select";

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.flex {
  display: flex;
}

.grid-12 {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
}

.section-title {
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 100%;
  color: var(--main-text-color);
}

.header {

  &__top {
    align-items: center;
    padding: 20px 0;
    position: relative;
  }

  &__top-1024 {
    display: none;
  }

  &__main-link {
    margin-right: 75px;
  }

  &__nav {
    margin-right: auto;
  }

  &__item:not(:last-child) {
    margin-right: 47px;
  }

  &__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: var(--main-text-color);

    @include link;
  }

  .header__search.search-active {
    display: flex;
  }

  &__search {
    position: absolute;
    z-index: 100;
    background-color: #fff;
    top: 26px;
    right: 131px;
    align-items: center;
    display: none;

    .search-inp {
      border: none;
      border-bottom: 1px solid var(--accent-color);
      outline: none;
      min-width: 278px;

      &::placeholder {
        font-weight: 400;
        font-size: 16px;
        line-height: 16px;
        color: #A1A6B4;
      }
    }

    .search-btn {
      height: 24px;
    }
  }

  &__search-btn {
    margin-right: 24px;
    outline: none;

    & svg path {
      transition: stroke .2s ease-in-out;
    }

    &:focus-visible svg path {
      stroke: var(--focus-color);
    }

    @media (min-width: 600px) {
      &:hover:not(:focus-visible):not(:active) svg path {
        stroke: var(--focus-color);
      }
    }

    &:active svg path {
      stroke: var(--active-color);
    }
  }

  &__entry-btn {
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    border: 2px solid var(--main-text-color);
    border-radius: 77px;
    padding: 11px 32px;
    transition: color .2s ease-in-out,
      background-color .2s ease-in-out,
      border-color .2s ease-in-out;

    &:focus-visible {
      background-color: var(--focus-color);
    }

    @media (min-width: 600px) {
      &:hover:not(:focus-visible):not(:active) {
        background-color: #121723;
        color: #fff;
      }
    }

    &:active {
      background-color: var(--active-color);
      border-color: var(--active-color);
      color: #fff;
    }
  }

  &__wrap {
    border-top: 1px solid #E6E8EC;
    border-bottom: 1px solid #E6E8EC;
  }

  &__low {
    padding-bottom: 10px;
    margin-right: 52px;
    align-items: center;
    padding-top: 4px;
  }

  &__item--low {
    margin-right: 46px;
  }

  &__list--low {
    align-items: center;
  }

  &__link--low {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--main-text-color);

    &:first-child {
      padding-left: 0
    }

    @include link;
  }

  &__ether {
    min-width: 175px;
    padding: 0 10px;
    border-left: 1px solid #E6E8EC;
    border-right: 1px solid #E6E8EC;
  }

  .ether-wrap {
    flex-direction: column;
  }

  &__btn-ether {
    align-self: center;
    max-height: 24px;
    margin-right: 10px;
    outline: none;

    svg path {
      transition: fill .2s ease-in-out;
    }

    &:focus-visible svg path {
      fill: var(--focus-color);
    }

    @media (min-width: 600px) {
      &:hover:not(:focus-visible):not(:active) svg path {
        fill: var(--accent-color);
      }
    }

    &:active svg path {
      fill: var(--active-color);
    }
  }

  .ether-text {
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: #A1A6B4;
  }

  &__pegi {
    width: 46px;
    height: 60px;
    background: #E6E8EC;
    justify-content: center;

    .round {
      width: 30px;
      height: 30px;
      border-radius: 50px;
      background-color: #fff;
      align-self: center;
      justify-content: center;

      .pegi-content {
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;
        color: #A1A6B4;
        align-self: center;
      }
    }
  }

  .modal.modal-open {
    display: block;
  }

  .modal {
    width: 824px;
    height: 655px;
    background-color: #121723;
    position: fixed;
    top: 19%;
    left: 28%;
    z-index: 10000;
    padding-left: 60px;
    padding-top: 60px;
    display: none;

    &__title {
      margin: 0;
      margin-bottom: 18px;
      font-size: 48px;
      line-height: 100%;
      color: #fff;
      font-weight: 400;
    }

    &__descr,
    &__text {
      margin: 0;
      font-size: 20px;
      line-height: 20px;
      color: #fff;
      margin-bottom: 30px;
    }

    &__close-btn {
      position: absolute;
      top: 30px;
      right: 30px;

      svg {

        circle,
        path {
          transition: stroke .2s ease-in-out;
        }
      }

      @media (min-width: 600px) {
        &:hover:not(:active) svg circle {
          stroke: var(--accent-color);
        }

        &:hover:not(:active) svg path {
          stroke: var(--accent-color);
        }
      }

      &:active svg circle {
        stroke: var(--active-color);
      }

      &:active svg path {
        stroke: var(--active-color);
      }
    }

    &__link {
      color: var(--accent-color);
      transition: color .2s ease-in-out;

      &:hover {
        color: var(--focus-color);
      }

    }

    &__form {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding-bottom: 30px;
      border-bottom: 1px solid rgba(230, 232, 236, 0.4);
      margin-bottom: 30px;
    }

    &__inp {
      @include form-item;
      width: 526px;
      padding: 25px 32px;
      border-radius: 40px;
      margin-bottom: 30px;
    }

    &__login-btn {
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
      color: #fff;
      background: #6D31EE;
      border-radius: 40px;
      padding: 10px 10px;
      padding: 24px 55px;
      transition: background-color .2s ease-in-out;

      @media (min-width: 600px) {
        &:hover:not(:active) {
          background-color: var(--focus-color);
        }

        &:active {
          background-color: var(--active-color);
        }
      }
    }

    &__social {
      display: flex;
    }

    &__item:not(:last-child) {
      margin-right: 48px;
    }

    &__item:first-child {

      .modal__social-btn {
        @media (min-width: 600px) {
          &:hover:not(:active) svg path {
            fill: var(--accent-color);
          }
        }

        &:active svg path {
          fill: var(--active-color);
        }
      }
    }

    &__social-btn {

      svg path {
        transition: fill .2s ease-in-out;
      }

      svg rect {
        transition: fill .2s ease-in-out;
      }

      @media (min-width: 600px) {
        &:hover:not(:active) svg rect {
          fill: var(--accent-color);
        }

        &:active svg rect {
          fill: var(--active-color);
        }
      }
    }
  }
}

.hero {
  padding-top: 92px;
  padding-bottom: 119px;
  background-image: url(../img/hero-bg.png);
  background-repeat: no-repeat;
  background-position: right 26% center;

  &__title {
    margin: 0;
    margin-bottom: 13px;
    font-style: normal;
    font-weight: 700;
    font-size: 60px;
    line-height: 100%;
    color: var(--main-text-color);

    &--mod {
      font-style: italic;
      font-weight: 400;
    }
  }

  &__content {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--main-text-color);
    max-width: 55%;
    margin-bottom: 70px;
  }

  &__link {
    padding: 25px 54px;
    color: #fff;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    background-color: var(--accent-color);
    border-radius: 40px;
    transition: background-color .2s ease-in-out,
      border-color .2s ease-in-out;
    outline: none;

    &:focus-visible {
      outline: 2px solid var(--active-color);
      background-color: var(--focus-color);
      outline-offset: -2px;
    }

    @media (min-width: 600px) {
      &:hover:not(:focus):not(:active) {
        background-color: var(--focus-color);
      }
    }

    &:active {
      background-color: var(--active-color);
    }
  }
}

.podcasts {
  padding-top: 94px;
  padding-bottom: 88px;
  background-color: #E6E8EC;

  &__title {
    margin: 0;
    margin-bottom: 30px;
  }

  &__wrap {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
    margin-bottom: 60px;
  }

  .hidden-card {
    display: none;
  }

  .hidden-card.open-card {
    display: flex;
  }

  &__card {
    grid-column: span 6;
    background-color: #fff;
    position: relative;

    &-pic {
      max-height: 140px;
    }

    &-content {
      padding-top: 24px;
      padding-left: 30px;
      flex-direction: column;
    }

    &-time {
      margin: 0;
      margin-bottom: 9px;
      font-weight: 400;
      font-size: 12px;
      line-height: 12px;
      color: var(--main-text-color);
    }

    &-title {
      margin: 0;
      margin-bottom: 3px;
      font-weight: 400;
      font-size: 20px;
      line-height: 20px;
      color: var(--main-text-color);
    }

    &-author {
      margin: 0;
      margin-bottom: auto;
      font-weight: 400;
      font-size: 16px;
      line-height: 16px;
      color: #A1A6B4;
    }

    &-btn {
      position: absolute;
      top: 30px;
      right: 30px;
      outline: none;

      & svg {
        transition: fill .2s ease-in-out;
      }

      & svg circle {
        transition: stroke .2s ease-in-out;
      }

      & svg path {
        transition: fill .2s ease-in-out;
      }

      & svg rect {
        transition: fill .2s ease-in-out;
      }

      &:focus-visible svg circle {
        stroke: var(--focus-color);
      }

      &:focus-visible svg path {
        fill: var(--focus-color);
      }

      &:focus-visible svg rect {
        fill: var(--focus-color);
      }

      @media (min-width: 600px) {

        &:hover:not(:focus-visible):not(:active) svg circle {
          stroke: var(--accent-color);
        }

        &:hover:not(:focus-visible):not(:active) svg path {
          fill: var(--accent-color);
        }

        &:hover:not(:focus-visible):not(:active) svg rect {
          fill: var(--accent-color);
        }
      }

      &:active svg circle {
        stroke: var(--active-color);
      }

      &:active svg path {
        fill: #fff;
      }

      &:active svg {
        fill: var(--active-color);
      }
    }

    &-date {
      position: absolute;
      left: 0;
      bottom: 0;
      background-color: #E6E8EC;
      font-weight: 400;
      font-size: 10px;
      line-height: 10px;
      padding: 3px 8px;
      color: var(--main-text-color);
    }

    &-activity {
      position: relative;
      padding-top: 10px;
      padding-bottom: 3px;

      &::after {
        content: "";
        position: absolute;
        inset: 0;
        left: -30px;
        width: 435px;
        border-top: 1px solid #E6E8EC;

      }
    }

    &-item {
      &:not(:last-child) {
        margin-right: 33px;
      }
    }
  }

  &__item-value {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #A1A6B4;
  }

  &__more-btn {
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    color: var(--accent-color);
    border: 2px solid var(--accent-color);
    border-radius: 40px;
    padding: 24px 53px;
    outline: none;
    transition: color .2s ease-in-out,
      background-color .2s ease-in-out,
      border-color .2s ease-in-out;

    &:focus-visible {
      background-color: var(--focus-color);
      color: #fff;
    }

    @media (min-width: 600px) {
      &:hover:not(:focus-visible):not(:active) {
        background-color: var(--accent-color);
        color: #fff;
      }
    }

    &:active {
      background-color: var(--active-color);
      border-color: var(--active-color);
      color: #fff;
    }
  }

  .podcasts__more-btn.hidden {
    display: none;
  }

  .btn-wrap {
    display: flex;
    justify-content: center;
  }
}

.broadcast {
  padding-top: 114px;
  padding-bottom: 88px;

  .broadcast-wrapper {
    grid-template-rows: 2;
  }

  &__wrap {
    grid-column: 1 / 4;
    grid-row: span 2;
    flex-direction: column;
    align-items: flex-start;
    margin-top: -13px;
    padding-bottom: 6px;
    background-image: url(../img/broadcast-bg.png);
    background-repeat: no-repeat;
    background-position: left bottom 130px;
  }

  &__right {
    grid-column: 4 / 13;
  }

  &__title {
    margin: 0;
    margin-bottom: 11px;
  }

  &__text {
    margin: 0;
    margin-bottom: 4px;
  }

  &__link {
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    color: var(--main-text-color);
    padding: 12px 56px;
    border: 1px solid var(--main-text-color);
    border-radius: 77px;
    margin-bottom: 11px;
    outline: none;
    transition: color .2s ease-in-out,
      background-color .2s ease-in-out,
      border-color .2s ease-in-out;

    &:focus-visible {
      background-color: var(--focus-color);
    }

    @media (min-width: 600px) {
      &:hover:not(:focus-visible):not(:active) {
        background-color: #121723;
        color: #fff;
      }
    }

    &:active {
      background-color: var(--active-color);
      border-color: var(--active-color);
      color: #fff;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #A1A6B4;
  }

  &__card {
    grid-column: 3 span;
    outline: none;

    &:focus-visible {
      .broadcast__card-link {
        background-color: var(--focus-color);
        color: #fff;

        svg path {
          fill: #fff;
        }
      }
    }

    @media (min-width: 600px) {
      &:hover:not(:focus-visible):not(:active) {
        .broadcast__card-link {
          color: var(--accent-color);

          svg path {
            fill: var(--accent-color);
          }
        }

        .broadcast__card-pic {
          opacity: 0.7;
        }
      }
    }

    &:active {
      .broadcast__card-link {
        color: var(--active-color);

        svg path {
          fill: var(--active-color);
        }
      }

      .broadcast__card-pic {
        opacity: 0.7;
      }
    }

    &-pic {
      transition: opacity .2s ease-in-out;
    }

    &-content {
      padding: 20px 0 20px 16px;
      border: 1px solid #E6E8EC;
      margin-top: -6px;
    }

    &-title {
      margin: 0;
      margin-bottom: 3px;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
      color: var(--main-text-color);
    }

    &-text {
      margin: 0;
      margin-bottom: 20px;
      font-weight: 400;
      font-size: 12px;
      line-height: 12px;
      color: #A1A6B4;
    }

    &-link {
      font-style: italic;
      font-weight: 400;
      font-size: 16px;
      line-height: 16px;
      color: var(--main-text-color);
      display: flex;
      align-items: center;
      max-width: 50%;
      outline: none;
      padding-top: 2px;
      padding-left: 3px;
      transition: color .2s ease-in-out,
        background-color .2s ease-in-out;

      svg path {
        transition: fill .2s ease-in-out;
      }
    }
  }
}

.guests {
  padding-top: 94px;
  padding-bottom: 90px;
  background-color: #121723;

  &__title {
    margin: 0;
    margin-bottom: 12px;
    color: #fff;
  }

  &__top-text {
    margin: 0;
    margin-bottom: 8px;
    color: #A1A6B4;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
  }

  &__low-text {
    margin: 0;
    margin-bottom: 50px;
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    color: #A1A6B4;
    max-width: 75%;
  }

  &__wrap {
    row-gap: 0;
  }

  &__accordion-wrapper {
    grid-column: 8 span;
  }

  &__social-link {
    outline: none;

    svg path {
      transition: fill .2s ease-in-out;
    }

    &:focus-visible svg path {
      fill: var(--focus-color);
    }

    @media (min-width: 600px) {
      &:hover:not(:focus-visible):not(:active) path {
        fill: var(--accent-color);
      }
    }

    &:active svg path {
      fill: var(--active-color);
    }
  }

  &__card {
    grid-column: 4 span;
    display: none;

    &-pic {
      margin-bottom: 5px;
    }

    &-social {
      margin-bottom: 7px;
    }

    &-item:not(:last-child) {
      margin-right: 30px;
    }

    &-title {
      margin: 0;
      margin-top: -1px;
      margin-bottom: 9px;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
      color: #fff;
    }

    &-descr {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 180%;
      color: #A1A6B4;
      margin-bottom: 41px;
    }

    &-link {
      color: var(--focus-color);
      padding: 10px 18px;
      border: 2px solid var(--focus-color);
      border-radius: 77px;
      font-weight: 500;
      font-size: 16px;
      line-height: 16px;
      outline: none;
      transition: color .2s ease-in-out,
        background-color .2s ease-in-out,
        border-color .2s ease-in-out;

      &:focus-visible {
        color: #fff;
        background-color: var(--accent-color);
        border-color: var(--accent-color);
      }

      @media (min-width: 600px) {
        &:hover:not(:focus-visible):not(:active) {
          color: #fff;
          background-color: var(--focus-color);
          border-color: var(--focus-color);
        }
      }

      &:active {
        color: #fff;
        background-color: var(--active-color);
        border-color: var(--active-color);
      }
    }
  }

  .accordion-control {

    @media (min-width: 600px) {
      &:hover:not(:focus-visible):not(:active) {
        .guests__accordion-title {
          color: var(--focus-color);
        }

        .accordion-span svg circle {
          stroke: var(--focus-color);
        }

        .accordion-span svg path {
          stroke: var(--focus-color);
        }
      }
    }

    &:active {
      .guests__accordion-title {
        color: var(--focus-color);
      }

      .accordion-span svg {
        fill: var(--focus-color);
      }

      .accordion-span svg circle {
        stroke: var(--focus-color);
      }

      .accordion-span svg path {
        stroke: #fff;
      }
    }
  }

  .accordion-span svg circle {
    transition: stroke .2s ease-in-out;
  }

  .accordion-span svg path {
    transition: stroke .2s ease-in-out;
  }

  &__accordion {
    position: relative;
    outline: none;

    &-title {
      margin: 0;
      margin-right: auto;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
      color: #fff;
      padding: 34px 0;
      border-top: 1px solid #A1A6B4;
      border-bottom: 1px solid #A1A6B4;
      cursor: pointer;
      width: 100%;
      transition: color .2s ease-in-out;
      outline: none;

      &:focus-visible {
        background-color: #292E39;
      }
    }

    &-content {
      grid-column: 1 / 9;
      max-height: 0;
      opacity: 0;
      overflow: hidden;
      will-change: max-height;
      transition: all .5s ease-in-out;
      box-sizing: content-box;
    }

    &-list {
      flex-direction: column;
      flex-wrap: wrap;
      max-height: 150px;
    }

    &-item {
      font-weight: 400;
      font-size: 16px;
      line-height: 16px;
      color: #FFFFFF;
      cursor: pointer;
      visibility: hidden;

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }
}

.open .accordion-control .guests__accordion-title {
  border-color: var(--focus-color);
}

.playlists {
  padding-top: 108px;
  padding-bottom: 94px;

  &__genres {
    grid-column: 4 span;
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    margin-top: -13px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #A1A6B4;
  }

  &__genres-wrapper {
    flex-direction: column;
    padding-bottom: 26px;
    border-bottom: 1px solid #D2D5DD;
    margin-bottom: 38px;
  }

  &__label {
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: var(--main-text-color);
    cursor: pointer;
    position: relative;
    padding-left: 43px;
    margin-bottom: 26px;
    outline: none;

    svg circle {
      transition: stroke .2s ease-in-out,
        fill .2s ease-in-out;
    }

    &:focus-visible svg circle {
      stroke: var(--accent-color);
      fill: #D2D5DD;
    }

    @media (min-width: 600px) {
      &:hover:not(:focus-visible):not(:active) svg circle {
        stroke: var(--accent-color);
      }
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__span {
    position: absolute;
    left: 0px;
    top: -5px;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-image: url(../img/inp-radio-active.svg);
      background-repeat: no-repeat;
      opacity: 0;
    }
  }

  &__inp {
    display: none;
  }

  .playlists__inp:checked+.playlists__span::after {
    opacity: 1;
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #A1A6B4;
  }

  &__copyright {
    font-style: italic;
    font-weight: 300;
    font-size: 16px;
    line-height: 22px;
    color: #A1A6B4;
    margin-bottom: 7px;
  }

  &__pegi {
    border: 1px solid #A1A6B4;
    border-radius: 100px;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #A1A6B4;
    width: 30px;
    height: 30px;
    align-items: center;
    justify-content: center;
  }

  &__wrap {
    grid-column: 8 span;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: 30px;
  }

  &__play {
    position: absolute;
    width: 48px;
    height: 48px;
    left: 59px;
    top: 59px;
    outline: none;
    opacity: 0;
    transition: opacity .2s ease-in-out,
      transform .2s ease-in-out;
  }

  .playlists__card-pic {
    transition: opacity .2s ease-in-out;

  }

  &__card {
    grid-column: 2 span;
    position: relative;
    max-height: 224px;
    outline: none;

    &:focus-visible {

      .playlists__play {
        opacity: 1;
      }

      .playlists__card-content {
        background-color: var(--focus-color);
        border-color: var(--focus-color);
      }

      .playlists__card-title,
      .playlists__card-text {
        color: var(--main-text-color);
      }

      .playlists__card-pic {
        opacity: 0.8;
      }
    }

    @media (min-width: 600px) {
      &:hover:not(:focus-visible):not(:active) {
        .playlists__play {
          opacity: 1;
        }

        .playlists__card-pic {
          opacity: 0.8;
        }

        .playlists__card-content {
          background-color: var(--accent-color);
          border-color: var(--accent-color);
        }

        .playlists__card-title,
        .playlists__card-text {
          color: #fff;
        }
      }
    }

    &:active {

      .playlists__play {
        opacity: 1;
        transform: scale(1.16);
      }

      .playlists__card-content {
        background-color: var(--active-color);
        border-color: var(--active-color);
      }

      .playlists__card-title,
      .playlists__card-text {
        color: #fff;
      }

      .playlists__card-pic {
        opacity: 0.8;
      }
    }

    &-content {
      border: 1px solid #E6E8EC;
      padding: 16px;
      margin-top: -6px;
      transition: background-color .2s ease-in-out,
        border-color .2s ease-in-out;
    }

    &-title {
      margin: 0;
      margin-bottom: 4px;
      font-weight: 500;
      font-size: 16px;
      line-height: 16px;
      text-align: start;
      color: var(--main-text-color);
      transition: color .2s ease-in-out;
    }

    &-text {
      margin: 0;
      font-weight: 400;
      font-size: 12px;
      line-height: 12px;
      text-align: start;
      color: #A1A6B4;
      transition: color .2s ease-in-out;
    }

    &-flag {
      display: flex;
      position: absolute;
      right: 0;
      top: 0;
      background-color: #121723;
      width: 24px;
      height: 20px;
      align-items: center;
      justify-content: center;
    }
  }
}

.product {
  padding-top: 94px;
  padding-bottom: 131px;
  background-color: #292E39;

  &__bg {
    grid-column: 7 span;
    background-image: url(../img/product-bg.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }

  &__sticker {
    grid-column: 2 span;
    background-image: url(../img/product-sticker.svg);
    background-repeat: no-repeat;
    min-height: 160px;
    max-width: 160px;
    background-position: left -5px top;
  }

  &__wrap {
    grid-column: 5 span;
    flex-direction: column;
    align-items: flex-start;
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    padding-bottom: 30px;
    font-weight: 400;
    font-size: 48px;
    line-height: 100%;
    color: #F0D288;
    background-image: url(../img/product-title-bg.svg);
    background-repeat: no-repeat;
    background-position: bottom;
  }

  &__text {
    margin: 0;
    margin-bottom: 13px;
    font-weight: 400;
    font-size: 16px;
    line-height: 180%;
    color: #fff;

    &:nth-child(3) {
      margin-bottom: 30px;
    }
  }

  &__price {
    margin-bottom: 28px;
    font-weight: 500;
    font-size: 30px;
    line-height: 30px;
    color: #fff;
  }

  &__link {
    font-weight: 500;
    font-size: 20px;
    line-height: 100%;
    color: #F0D288;
    padding: 25px 10px 24px 40px;
    border: 2px solid #F0D288;
    border-radius: 100px;
    position: relative;
    outline: none;
    transition: all .2s ease-in-out;

    svg path {
      transition: fill .2s ease-in-out;
    }

    &:focus-visible {
      background-color: #BB973E;
      color: #121723;
    }

    @media (min-width: 600px) {
      &:hover:not(:focus-visible):not(:active) {
        background-color: #F0D288;
        color: #121723;

        svg .path-1 {
          fill: #121723;
        }

        svg .path-2 {
          fill: #F0D288;
        }
      }
    }

    &:active {
      background-color: #BB973E;
      border-color: #BB973E;
      color: #121723;

      svg .path-1 {
        fill: #121723;
      }

      svg .path-2 {
        fill: #BB973E;
      }
    }

    &-icon {
      position: absolute;
      left: 11px;
    }
  }
}

.about {
  padding-top: 94px;
  padding-bottom: 93px;
  background-color: #121723;

  &__wrap {
    grid-column: 7 span;
    margin-bottom: 30px;
  }

  &__bg--left {
    grid-column: 2 span;
    background-image: url(../img/about-left-bg.svg);
    background-repeat: no-repeat;
  }

  &__bg--right {
    grid-column: 3 span;
    background-image: url(../img/about-right-bg.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 13px;
    font-weight: 400;
    font-size: 48px;
    line-height: 100%;
    color: #fff;
  }

  &__text--up {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: #A1A6B4;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    color: #A1A6B4;
  }

  &__card {

    &-content {
      padding: 24px 10px 0 0px;
    }

    &-title {
      margin: 0;
      margin-bottom: 7px;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
      color: #fff;
    }

    &-text {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #A1A6B4;
    }
  }

  &__form {
    grid-column: 10 span;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    column-gap: 30px;
    row-gap: 25px;
    flex-direction: column;
    align-items: flex-start;

    &-label-textarea {
      grid-column: 10 span;
      position: relative;
    }

    &-label-inp {
      grid-column: 5 span;
      position: relative;
    }

    &-textarea {
      width: 100%;
      resize: none;
      padding: 30px;
      min-height: 150px;
      border-radius: 30px;

      @include form-item;
    }

    &-inp {
      width: 100%;
      @include form-item;
      padding: 25px;
      padding-left: 30px;
      border-radius: 40px;
    }

    &-btn {
      margin-bottom: 8px;
      padding: 25px 54px;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;
      color: #fff;
      background: var(--accent-color);
      border-radius: 40px;
      grid-column: 1/4;
      max-width: 84%;
      outline: none;
      transition: background-color .2s ease-in-out;

      &:focus-visible {
        background-color: var(--focus-color);
        outline: 2px solid var(--active-color);
        outline-offset: -2px;
      }

      @media (min-width: 600px) {
        &:hover:not(:focus-visible):not(:active) {
          background-color: var(--focus-color);
        }
      }

      &:active {
        background-color: var(--active-color);
      }
    }

    &-label {
      display: flex;
      align-items: center;
      grid-column: 1 / 5;
      font-weight: 500;
      font-size: 12px;
      line-height: 12px;
      color: #fff;
      user-select: none;
      max-width: 85%;
      cursor: pointer;
    }

    &-check {
      display: none;
    }

    .checkbox-frame {
      display: inline-block;
      width: 14px;
      height: 13px;
      border: 1px solid #fff;
      margin-right: 10px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translateX(50%) translateY(-50%);
        width: 10px;
        height: 10px;
        background-image: url(../img/checkbox-arrow.svg);
        background-repeat: no-repeat;
        opacity: 0;
      }
    }

    &-check:checked+.checkbox-frame::after {
      opacity: 1;
    }
  }
}

.footer {
  padding-top: 94px;
  padding-bottom: 10px;
  background-color: #292E39;

  &__container {
    padding-bottom: 92px;
  }

  &__left {
    grid-column: 7 span;
    display: flex;
    flex-direction: column;
  }

  &__right {
    grid-column: 5 span;
    margin-left: -5px;
  }

  &__main-link {
    margin-bottom: 14px;
  }

  &__social {
    display: flex;

    &-item:not(:last-child) {
      margin-right: 21px;
    }
  }

  &__nav-list:not(:last-child) {
    margin-right: 97px;
  }

