.choices {
  &::after {
    z-index: 100;
  }

  &:focus-visible {
    outline: 1px solid #6D31EE;
    border-radius: 10px;
  }
}

.choices__inner {
  width: 100%;
  background: #E6E8EC;
  border-radius: 10px;
  border: none;
  padding: 9px 0 2px 8px;
  position: relative;
  z-index: 10;
  font-family: 'Muller';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #121723;

  &:hover {
    background-color: #F5F6F7;
  }

  &:active {
    background-color: #D2D5DD;
  }
}

.choices__placeholder {
  opacity: 1;
  font-family: 'Muller';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #121723;
}

.is-flipped.is-open .choices__inner {
  border-radius: 10px
}

.is-open .choices__inner {
  border-radius: 10px
}

.is-flipped .choices__list--dropdown,
.is-flipped .choices__list[aria-expanded] {
  top: auto;
  bottom: 100%;
  margin-top: 0;
  margin-bottom: -10px;
  border-radius: 10px;
  border-color: #D2D5DD;
}

.choices__list--dropdown,
.choices__list[aria-expanded] {
  background-color: #F5F6F7;
  border: 1px solid #D2D5DD;
  margin-top: -10px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.is-open .choices__list--dropdown,
.is-open .choices__list[aria-expanded] {
  border-color: #D2D5DD;
  padding-top: 10px;
}

.choices[data-type*=select-one]::after {
  background-image: url(../img/selector-arrow.svg);
}

.choices__list--dropdown .choices__item--selectable,
.choices__list[aria-expanded] .choices__item--selectable {
  border-bottom: 1px solid #D2D5DD;
  font-family: 'Muller';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;

  &:last-child {
    border: none;
  }
}

.choices__list--dropdown .choices__item--selectable.is-highlighted,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  background-color: #F5F6F7;
  color: #121723;

  &:focus-visible {
    background-color: #AA9DFA;
  }
}

.choices {
  margin-bottom: auto;
}
