@mixin form-item {
  background: transparent;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  color: #fff;
  outline: none;
  border: 1px solid #FFFFFF;
  transition: background-color .2s ease-in-out,
  border-color .2s ease-in-out;

  &:focus-visible {
    background-color: #292E39;
    border-color: #AA9DFA;
  }

  &:hover:not(:focus-visible):not(:active) {
    background-color: #292E39;
  }

  &::placeholder {
    color:#A1A6B4;
  }
}
