@media (max-width: 1260px) {

  .container {
    max-width: 855px;
  }

  .container-1024 {
    display: block;
  }

  .grid-12 {
    grid-template-columns: repeat(6, 1fr);
  }

  .header {

    .modal {
      top: 80px;
      left: 100px;
    }

    &__main-link {
      display: none;
    }

    &__top-1024 {
      display: block;
      background-color: #E6E8EC;
      padding-top: 23px;
      padding-bottom: 18px;

      .header__entry-btn {
        display: none;
      }
    }

    &__low {
      margin-right: 0px;
      padding-top: 9px;
      padding-bottom: 11px;
    }

    &__main-link--1024 {
      height: 14px;
    }

    &__logo {
      height: 14px;
    }

    &__nav {
      margin-left: -5px;
    }

    &__list--low {
      margin-right: 8px;
    }

    &__item--low {
      margin-right: 22px;
    }

    &__ether {
      min-width: 151px;
    }

    &__pegi {
      display: none;
    }
  }

  .hero {
    padding-bottom: 115px;
    background-position: right 13% center;
    background-size: 25%;

    &__content {
      max-width: 65%;
      margin-bottom: 46px;
    }
  }

  .broadcast {

    padding-top: 110px;
    padding-bottom: 93px;

    &__wrap {
      grid-column: 2 span;
      padding-bottom: 0;
    }

    &__card {
      grid-column: 2 span;

      &:nth-child(6) {
        grid-column: 3 / 5;
      }

      &:nth-child(7) {
        grid-column: 5 / 7;
      }

      &-content {
        margin-top: -8px;
        padding-left: 12px;
      }
    }
  }

  .guests {

    &__low-text {
      max-width: 65%;
    }

    &__accordion-wrapper {
      grid-column: 4 span;
    }

    &__accordion-list {
      max-height: 185px;
    }

    &__accordion-item {
      margin-right: 34px;
    }

    &__card {
      grid-column: 2 span;
    }
  }

  .playlists {
    &__genres {
      grid-column: 6 span;
      flex-wrap: wrap;
      align-content: flex-start;
    }

    &__genres-wrapper {
      flex-wrap: wrap;
      max-height: 190px;
      padding: 0;
      border: none;
      margin: 0;
      margin-bottom: 5px;
    }

    &__label {
      margin-right: 34px;

      &:nth-child(4) {
        margin-bottom: 0;
      }
    }

    &__descr,
    &__copyright,
    &__pegi {
      display: none;
    }

    &__wrap {
      grid-template-columns: repeat(6, 1fr);
      margin-bottom: 12px;
    }

    &__card {
      display: flex;
      max-height: 112px;

      &-content {
        margin: 0;
        padding: 0 9px;
        height: 100%;
        min-width: 143px;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }

      &-flag {
        left: 0;
      }
    }

    &__info-1024 {
      display: block;
      grid-column: 6 span;
      border-top: 1px solid #D2D5DD;
      padding-top: 38px;
    }

    &__info-text {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: #A1A6B4;
      width: 102%;
    }

    &__info-copyright {
      font-style: italic;
      font-weight: 300;
      font-size: 16px;
      line-height: 22px;
      color: #A1A6B4;
      margin: 0;
      margin-bottom: 6px;
    }

    .pegi-1024 {
      display: flex;
    }

    &__play {
      top: 32px;
      left: 32px;
    }
  }

  .product {
    padding-bottom: 92px;

    &__bg,
    &__wrap {
      grid-column: 3 span;
    }

    &__bg {
      background-position: center 123px;
    }

    &__sticker {
      min-height: 100%;
      max-width: 100%;
      background-size: 100px;
      background-position: left top 119px;
    }
  }

  .about {

    &__wrap {
      grid-column: 4 span;
    }

    &__bg--left {
      display: none;
    }

    &__bg--right {
      grid-column: 2 span;
      background-position: center 49px;
    }

    .button-wrap {
      margin-bottom: 8px;
    }

    &__form-btn {
      max-width: 94%;
    }

    &__form-label {
      grid-column: 1/6;
    }

    .swiper-button-next,
    .swiper-rtl .swiper-button-prev {
      right: -52px;
    }

    .swiper-button-prev,
    .swiper-rtl .swiper-button-next {
      left: -50px;
    }
  }

  .footer {

    &__left {
      grid-column: 2 span;
    }

    &__right {
      grid-column: 4 span;
      padding-left: 93px;
    }

    &__nav-item:not(:last-child) {
      margin-bottom: 18px;
    }

  }
}

@media (max-width: 860px) {
  .container {
    max-width: 570px;
  }

  .grid-12 {
    grid-template-columns: repeat(4, 1fr);
  }

  .header {

    .modal {
      width: 540px;
      top: 80px;
      left: 115px;

      &__inp {
        width: 460px;
        padding-left: 40px;
      }
    }


    .menu-open {
      transform: none;
    }

    &-low-wrap {
      display: none;
    }

    &__menu {
      display: flex;
      width: 100%;
      height: 100vh;
      background-color: #D2D5DD;
      padding-left: 113px;
      background-image: url(../img/menu-bg.svg);
      background-repeat: no-repeat;
      background-position: bottom right 39%;
      position: fixed;
      top: 0;
      z-index: 1000;
      transform: translateY(-100%);
      transition: transform .3s ease-in-out;

      .header__nav {
        display: block;
        padding-top: 28px;
      }

      .menu__close-btn {
        padding-left: 6px;
        margin-bottom: 50px;
      }

      .header__list {
        flex-direction: column;
        flex-wrap: wrap;
        max-height: 330px;
        align-content: flex-start;
      }

      .header__item {
        margin-bottom: 64px;
      }

      .menu__bg {
        height: 100%;
        width: 35%;
        background-image: url(../img/menu-bg.svg);
        background-repeat: no-repeat;
      }

      .header__item:not(:last-child) {
        margin-right: 36px;
      }
    }

    &__low {
      padding-top: 15px;
      padding-bottom: 17px;
    }

    &__nav {
      display: none;
    }

    &__menu-btn {
      display: block;
      width: 24px;
      height: 24px;
      position: relative;
      margin-right: auto;
    }

    .menu-item {
      position: absolute;
      top: 46%;
      width: 100%;
      height: 3px;
      display: block;
      background-color: #6D31EE;

      &:first-child {
        top: 0;
      }

      &:last-child {
        top: 84%;
      }
    }

    &__ether {
      display: none;
    }

    &__play-btn-768 {
      display: block;
      height: 24px;
    }

    &__list--low {
      margin-right: 18px;
    }

    &__item--low {
      margin-right: 23px;

      &:last-child {
        margin-right: 0;
      }
    }

  }

  .hero {
    background-image: none;

    &__content {
      max-width: none;
    }
  }

  .podcasts {

    &__wrap {
      grid-template-columns: repeat(4, 1fr);
    }

    &__card {
      grid-column: 4 span;
    }
  }

  .broadcast {
    padding-bottom: 29px;

    &-wrapper {
      grid-template-columns: repeat(4, 1fr);
      margin-bottom: 59px;
    }

    &__wrap {
      grid-column: 4 span;
      background-image: none;
      margin-bottom: 16px;
    }

    &__card {

      &:nth-child(7) {
        grid-column: 2 span;
      }

      &:nth-child(6) {
        grid-column: 2 span;
      }
    }

    &__link,
    &__descr {
      display: none;
    }

    .choices[data-type*=select-one] {
      width: 47%;
    }

    &__archive-768 {
      display: flex;
      flex-direction: column;
      grid-column: 4 span;
      align-items: center;
      padding-top: 29px;
      border-top: 1px solid #E6E8EC;
      background-image: url(../img/broadcast-bg.png);
      background-repeat: no-repeat;
      background-position: left 30% bottom;
      background-size: 71px;

      .broadcast__link {
        display: block;
      }

      .broadcast__descr {
        display: block;
        max-width: 45%;
        padding-left: 92px;
      }
    }

    &__link {
      margin-bottom: 8px;
    }
  }

  .guests {
    padding-bottom: 106px;

    &__low-text {
      max-width: none;
    }

    &__accordion-wrapper {
      margin-bottom: 46px;
    }

    &__accordion-list {
      max-height: 230px;
    }

    &__accordion-item {

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    .open .guests__accordion-content {
      padding-bottom: 0;
    }

    &__card {
      grid-column: 4 span;

      &-descr {
        max-width: 75%;
      }
    }
  }

  .playlists {

    &__genres {
      grid-column: 4 span;

      &-wrapper {
        max-height: 210px;
        margin-bottom: 24px;
      }
    }

    &__label {
      margin-bottom: 40px;
    }

    &__wrap {
      grid-column: 4 span;
      grid-template-columns: repeat(4, 1fr);

    }

    &__info-1024 {
      grid-column: 4 span;
    }
  }

  .product {
    padding-top: 54px;

    &__bg {
      grid-column: 4 span;
      min-height: 474px;
      background-position: center;
      margin-bottom: 21px;
    }

    &__wrap {
      grid-column: 4 span;
    }

    &__sticker {
      background-size: 160px;
      background-position: left top;
    }

    &__title {
      max-width: 90%;
      background-image: none;
      padding-bottom: 0;
      margin-bottom: 17px;
    }

    &__text {
      &:nth-child(2) {
        background-image: url(../img/product-line.svg);
        background-position: top;
        background-repeat: no-repeat;
        padding-top: 32px;
      }
    }
  }

  .about {

    &__bg--left,
    &__bg--right {
      display: none;
    }

    .about__wrap-768 {
      display: flex;
      margin-bottom: 30px;
    }

    &__left-768,
    &__right-768 {
      height: 112px;
    }

    &__left-768 {
      width: 70%;
      background-image: url(../img/about-left-768.svg);
      background-repeat: no-repeat;
    }

    &__right-768 {
      background-image: url(../img/about-right-bg.svg);
      width: 30%;
      background-size: 115px;
      background-repeat: no-repeat;
    }

    .button-wrap {
      margin-bottom: 25px;
    }

    &__form-label-inp {
      grid-column: 10 span;

      &:nth-child(3) {
        margin-bottom: 6px;
      }
    }

    &__form-btn {
      grid-column: 1/6;
      max-width: 83%;
    }

    &__form-label {
      grid-column: 1/8;
    }
  }

  .footer {

    &__left {
      grid-column: 4 span;
      align-items: center;
      margin-bottom: 44px;
    }

    &__right {
      padding-left: 0;
    }

    &__nav {
      justify-content: space-between;
    }

    &__nav-list:not(:last-child) {
      margin-right: 0;
    }

    &__nav-item:not(:last-child) {
      margin-bottom: 27px;
    }

    &__social-link svg {
      width: 36px;
      height: 36px;
    }

    &__container {
      padding-bottom: 79px;
    }
  }
}

