    &__title {
      font-size: 24px;
      line-height: 100%;
      max-width: 100%;
      margin-bottom: 11px;
    }

    &__text {
      font-size: 14px;
      line-height: 160%;
      margin-bottom: 9px;

      &:nth-child(2) {
        padding-top: 29px;
      }

      &:nth-child(3) {
        margin-bottom: 10px;
      }
    }

    &__price {
      font-size: 24px;
      line-height: 24px;
      margin-bottom: 20px;
    }

    &__link {
      font-size: 16px;
      line-height: 100%;
      padding: 14px 12px 15px 44px;
    }
  }

  .about {
    padding-top: 55px;
    padding-bottom: 53px;

    .about__wrap-768 {
      display: none;
    }

    &__title {
      font-size: 24px;
      margin-bottom: 11px;
    }

    &__text--up,
    &__text {
      font-size: 14px;
    }

    &__text--up {
      margin-bottom: 3px;
    }

    &__card img {
      width: 113px;
      height: 113px;
    }

    &__card-content {
      padding: 4px 0 0 0;
    }

    &__card-title {
      font-size: 14px;
      line-height: 18px;
    }

    &__card-text {
      font-size: 12px;
      line-height: 16px;
    }

    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }

    .swiper {
      width: 113%;
      height: 216px;
    }

    .swiper-slide {
      width: 113px !important;
    }

    &__form {

      &-label-textarea {
        width: 95%;
      }

      .name-inp:first-child {
        margin-bottom: 5px;
      }

      &-textarea {
        border-radius: 20px;
        padding: 15px 24px;

        &::placeholder {
          font-size: 16px;
          line-height: 16px;
        }
      }

      &-inp {
        padding: 14px;
        padding-left: 23px;
        width: 95%;

        &::placeholder {
          font-size: 16px;
          line-height: 16px;
        }
      }

      &-btn {
        font-size: 16px;
        line-height: 16px;
        grid-column: 1/9;
        padding: 16px 44px;
        margin-bottom: -3px;
      }

      &-label {
        font-size: 9px;
        line-height: 9px;
        grid-column: 10 span;
      }
    }

  }

  .footer {
    padding-top: 54px;

    &__link {
      font-size: 12px;
      line-height: 12px;
    }

    &__nav-item:not(:last-child) {
      margin-bottom: 18px;
    }

    &__right {
      display: none;
    }

    &__nav-list-320 {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      height: 216px;
      padding-top: 25px;
      padding-left: 25px;
      align-content: flex-start;
      background-color: #121723;
    }

    &__nav-item {
      margin-right: 68px;
    }

    &__container {
      padding-bottom: 5px;
    }

    &__copyright {
      background-color: #292E39;
      padding-top: 30px;
      background-position: top 9px right -9px;
    }

    &__copyright-item {
      font-size: 12px;
      line-height: 103.34%;
    }
  }

  .broadcast .choices[data-type*=select-one] {
    width: 100%;
  }
}
